@import '../../global.scss';

.timeline {

  h3 {
    font-family: Open Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 22px;
    line-height: 30px;
    color: #ffffff;
    margin-left: 66px;
    margin-top: 40px;
  }

  label {
    font-family: Open Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    /* identical to box height */
    margin-left: 66px;
    margin-top: 10px;

    color: #ffffff;
  }

  .box {

    .container {
      width: 100%;
      display: flex;

      .lines {
        margin-left: 40px;
        margin-top: 12px;

        .dot {
          width: 14px;
          height: 14px;
          border-radius: 7px;
          @include mobile{
            font-size: 10px;
          }
        }
        :nth-child(1){
          background: purple; 
        }
        .dot:nth-child(3){
          background: darkolivegreen; 
        }
        .dot:nth-child(5){
          background: darkkhaki; 
        }
        .line {
          height: 103px;
          width: 2px;
          background: $mainColor;
          margin-left: 5.3px;
          @include mobile{
            height: 31px;
          }
        }
      }
      .cards {
        margin-left: 12px;
        transform: translateY(-50px);
        margin-top: 131px;
        .card {
          width: 249px;
          height: 93px;
          padding-top: 25px;
          background: #ffffff;
          box-shadow: 0 2px 2px 0 #eeeeee40;
          border-radius: 10px;

          box-shadow: 0px 16px 15px -10px rgba(105, 96, 215, 0.0944602);
          margin-bottom: 10px;

          @include mobile{
            height: 31px;
            padding-top: 10px;
          }

          h2{
            font-size: 16px;
            @include mobile{
              font-size: 10px;
            }
          }

          p{
            @include mobile{
              font-size: 10px;
            }
          }

          &.mid {
            height: 71px;
            @include mobile{
              height: 30px;
            }
          }
        }
      }
    }

    .bottom {
      width: 100%;
      height: 107px;
      background: #fff;
      box-shadow: 0 0 2px #eeeeee50;
      padding-top: 45px;

      .btn {
        width: 249px;
        height: 62px;
        background: #ffffff40;
        mix-blend-mode: normal;
        cursor: pointer;
        border: 1px solid #8260d780;
        border-radius: 5px;

        display: flex;
        align-items: center;
        justify-content: center;
        font-family: Open Sans;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 19px;
        color: #2b2862;

        margin-left: 53px;
        transition: 0.3s;
        background: #2b2862;
        color: #fff;
        border-color: #2b2862;

        &:hover {
          transform: scale(1.03);
        }
      }
    }
  }
}
